<template>
	<view>
			<StyleList3 v-for="(item,index) in list" :key="index" :videoTitle="item.title"
				:publishTime="item.created_time" :hits="item.play_count" :danMus="item.danmu_count" :cover="item.cover">
			</StyleList3>
		<view v-if="list.length==0" class="flex  flex-column  justify-center align-center mt-5">没有数据</view>
	</view>
</template>

<script>
	import StyleList3 from '@/componets/StyleList3/StyleList3.vue';
	export default {
		data() {
			return {
				category_id: "",
				page: 1,
				list: []
			}
		},
		components: {
			StyleList3
		},
		
		onPullDownRefresh() {
			this.page = 1
			this.getSort().then(res=>{
				uni.showToast({
					title: '刷新成功',
					icon: 'none'
				});
				uni.stopPullDownRefresh()
			}).catch(err=>{
				uni.showToast({
					title: '刷新失败',
					icon: 'none'
				});
				uni.stopPullDownRefresh()
			})
		},
		
		onReachBottom() {
			this.page=this.page+1
			this.getSort()
			
		},
		
		
		methods: {
			getSort() {
				let url = `/category/${this.category_id}/video/${this.page}`
				let page = this.page
			 	this.$selfApi.get(url).then(res => {
					if(page==1){
						this.list = res.rows
					}else{
						this.list=[...this.list,...(res.rows)]
					}
				}).catch(err=>{
					if(this.page>1){
						this.page--
					}
				})
			}
		},
		onLoad(e) {
			this.category_id = e.id
			uni.setNavigationBarTitle({
				title: e.title
			})
			this.getSort()
		},
		


	}
</script>

